/* css reset */
*{box-sizing: border-box;}
*:before,*:after{box-sizing: border-box;}
*{margin:0; padding:0;}
ul,ol{ list-style: none; }
a{color: inherit; text-decoration: none;}
img{
  width: 24px;
  height: 24px;
}

/* style */
body {
  background-color: rgb(238, 238, 238);
}
.globalHeader {
  border: 1px solid #eee;
  border-radius: 6px;
  overflow: hidden;
  margin: 215px 30px 36px 30px;
}
.searchForm {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 500px) {
  .searchForm {
    max-width: 561px;
    margin-left: auto;
    margin-right: auto;
  }
}
.searchForm > input {
  outline: none;
  width: 86%;
  height: 46px;
  padding-left: 10px;
  border: none;  
}

.searchForm .search-icon {
  padding: 0 3px;
  width: 14%;
  background-color: #fff;
  border: none;
  white-space: nowrap;
}
.searchForm .search-icon .icon {
  width: 24px;
  height: 24px;
}

.globalMain {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.siteList {
  display: flex;
  flex-wrap: wrap;
  margin: 5px;
  justify-content: center;
}
.siteList > li {
  margin-bottom: 8px;
}
.siteList .site {
  width: 112px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 6px;
  padding: 13.5px 0;
  position: relative;
}
.siteList .site > .logo {
  cursor: pointer;
  margin-top: 16px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(254, 255, 255);
}

.siteList .site > .title {
  font-weight: 400;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  margin-top: 4px;
  font-size: 13px;
  direction: ltr;
  padding: 0 8px;
}
.siteList .site > .close {
  position: absolute;
  right: 0;
  top: 0;
  border: none;
  display: none;
  cursor: pointer;
  /*border-radius: 50%;*/
  /*overflow: hidden;*/
}
.siteList .site:hover > .close {
  display: block;
}
.siteList .site .close > .icon {
  width: 18px;
  height: 18px;
  /*display: flex;*/
  /*background-color: #cecece;*/
}
.siteList .addButton {
  cursor: pointer;
  width: 112px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 13.5px 0;
}
.siteList .addButton .icon {
  width: 24px;
  height: 24px;
}
.siteList .addButton .text {
  font-weight: 400;
  overflow: hidden;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  margin-top: 4px;
  font-size: 13px;
  direction: ltr;
  padding: 0 8px;
}
.siteList .addButton .iconWrapper {
  margin-top: 16px;
  width:48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: rgb(254, 255, 255);
}